const state = {
    activeSheet:{},
    activeSheetId:'',
    uploadInProgress:false,
    uploadPercentage:'',
    instructorOverride:''
}

import * as firebase from 'firebase'

var database;

  // getters
const getters = {}

// actions
const actions = {
    addEntrySheet({ commit }, entry){
        if(!database){
            database = firebase.database();
        }
        let newSheetRef = database.ref('users/' + entry.userUID + '/sheets').push();
        //initialize week
        var d = new Date();
        d.setDate(d.getDate() + (1 + 7 - d.getDay()) % 7);
        let weekParts = getWeekNumber(d);

        entry.entry.week = weekParts[0]+'-W'+weekParts[1];

        //find next monday and get week
        newSheetRef.set(entry.entry);
        commit('setActiveSheetId',newSheetRef.key);
        commit('setActiveSheet',entry.entry);
        return newSheetRef.key;
    },
    updateShare({ commit }, info){
        if(!database){
            database = firebase.database();
        }
        database.ref('users/' + info.uid + '/sheets/' + info.sheetId).update({
            sharedWithInstructor:info.shared
        });
        commit('updateShare',info.shared);
    },
    updateWeek({ commit }, info){
        if(!database){
            database = firebase.database();
        }
        database.ref('users/' + info.uid + '/sheets/' + info.sheetId).update({
            week:info.week
        });
        commit('updateWeek',info.week);
    },
    updatePieceNotes({ commit }, info){
        if(!database){
            database = firebase.database();
        }
        database.ref('users/' + info.uid + '/sheets/' + info.sheetId + '/' + info.pieceIndex).update({
            notes:info.notes
        });
        commit('updatePieceNotes',{index:info.pieceIndex,notes:info.notes});
    },
    updateMeasurePracticeTime({ commit }, info){
        if(!database){
            database = firebase.database();
        }
        let {sheetRef,sheet,measureIndex, strategyIndex, ind, val } = info;
        sheet = JSON.parse(JSON.stringify(sheet));
        if(measureIndex !== null){
            if(!sheet.measures[measureIndex].strategies[strategyIndex].practice){
                sheet.measures[measureIndex].strategies[strategyIndex].practice = {};
            }      
            if(!sheet.measures[measureIndex].strategies[strategyIndex].practice[ind]){
                sheet.measures[measureIndex].strategies[strategyIndex].practice[ind] = {
                    time:''
                };
            }
            sheet.measures[measureIndex].strategies[strategyIndex].practice[ind].time = val;
        }
        else {
            if(!sheet.strategies[strategyIndex].practice){
                sheet.strategies[strategyIndex].practice = {};
            }
            if(!sheet.strategies[strategyIndex].practice[ind]){
                sheet.strategies[strategyIndex].practice[ind] = {};
            }
            sheet.strategies[strategyIndex].practice[ind].time = val;
        }
        database.ref('users/' + info.uid + '/sheets/' + info.sheetId + '/' + sheetRef).update(sheet);
    },
    uploadVid({ commit }, info){
        if(!database){ //initialize this once on login, call rootstate to access one database perhaps?
            database = firebase.database();
        }
        //        var ref = storage.ref(info.userId+'/noob.mp4');

        commit('setUploadInProgress',true);
        let storage = firebase.storage();
        var ref = storage.ref('videos/'+info.userId+'/'+uuidv4());
        let uploadRef = ref.put(info.vid);

        if(this.state.sheet.instructorOverride){
            info.userId = this.state.sheet.instructorOverride;
        }

        uploadRef.then(function(snapshot) {
            console.log('Uploaded a blob or file!');
            snapshot.ref.getDownloadURL().then(function(downloadURL) {
                console.log("File available at", downloadURL);
                info.downloadURL = downloadURL;
                let uploadVideoRef;
                //info.uploadInfo
                if(info.uploadInfo.type === 'source'){
                    uploadVideoRef = database.ref('users/' + info.userId + '/sheets/' + info.sheetId + '/' + info.uploadInfo.index + '/videos').push();
                }
                else if(info.uploadInfo.type === 'sourceStrat'){
                    uploadVideoRef = database.ref('users/' + info.userId + '/sheets/' + info.sheetId + '/' + info.uploadInfo.index + '/strategies/' + info.uploadInfo.stratIndex + '/videos').push();
                }
                else if(info.uploadInfo.type === 'measure'){
                    uploadVideoRef = database.ref('users/' + info.userId + '/sheets/' + info.sheetId + '/' + info.uploadInfo.index + '/measures/' + info.uploadInfo.measureIndex + '/videos').push();    
                }
                else if(info.uploadInfo.type === 'measureStrat'){
                    uploadVideoRef = database.ref('users/' + info.userId + '/sheets/' + info.sheetId + '/' + info.uploadInfo.index + '/measures/' + info.uploadInfo.measureIndex + '/strategies/' + info.uploadInfo.stratIndex + '/videos').push();     
                }

                if(!uploadVideoRef){
                    return false;
                }

                uploadVideoRef.set(downloadURL).then(()=>{
                    info.key = uploadVideoRef.key;
                    commit('updateSheetVideos', info);
                    //commit here update sheet
                },(err)=>{
                    console.log(err);
                    alert('err uploading video, please contact support.');  
                });
            });
            commit('setUploadInProgress',false);
        }, err =>{
            commit('setUploadInProgress',false);
            debugger;
            console.log('err');
        });

        uploadRef.on('state_changed', function(snapshot){
            // Observe state change events such as progress, pause, and resume
            // Get task progress, including the number of bytes uploaded and the total number of bytes to be uploaded
            var progress = (snapshot.bytesTransferred / snapshot.totalBytes) * 100;
            console.log('Upload is ' + progress + '% done');
            commit('setUploadPercentage',progress.toFixed(1));
        });

    }
}

// mutations
const mutations = {
    setActiveSheet (state, activeSheet) {
      state.activeSheet = activeSheet;
    },
    setActiveSheetId (state, id){
        state.activeSheetId = id;
    },
    updateShare (state, share){
        state.activeSheet.sharedWithInstructor = share;
    },
    updateWeek (state, week){
        state.activeSheet.week = week;
    },
    updatePieceNotes (state, info){
        state.activeSheet[info.index].notes = info.notes;
    },
    updateSheetVideos (state,info){
        let videos;
        if(info.uploadInfo.type === 'source'){
            videos = {...state.activeSheet[info.uploadInfo.index].videos};
        }
        if(info.uploadInfo.type === 'sourceStrat'){
            videos = {...state.activeSheet[info.uploadInfo.index].strategies[info.uploadInfo.stratIndex].videos};    
        }
        if(info.uploadInfo.type === 'measure'){
            videos = {...state.activeSheet[info.uploadInfo.index].measures[info.uploadInfo.measureIndex].videos};
        }
        if(info.uploadInfo.type === 'measureStrat'){
            videos = {...state.activeSheet[info.uploadInfo.index].measures[info.uploadInfo.measureIndex].strategies[info.uploadInfo.stratIndex].videos};
        }
        if(!videos){
            videos = {};
        }
        videos[info.key] = info.downloadURL;
        if(info.uploadInfo.type === 'source'){
            state.activeSheet[info.uploadInfo.index].videos = videos;
        }
        if(info.uploadInfo.type === 'sourceStrat'){
            state.activeSheet[info.uploadInfo.index].strategies[info.uploadInfo.stratIndex].videos = videos;    
        }
        if(info.uploadInfo.type === 'measure'){
            state.activeSheet[info.uploadInfo.index].measures[info.uploadInfo.measureIndex].videos = videos;    
        }
        if(info.uploadInfo.type === 'measureStrat'){
            state.activeSheet[info.uploadInfo.index].measures[info.uploadInfo.measureIndex].strategies[info.uploadInfo.stratIndex].videos = videos;  
        }
    },
    setUploadInProgress(state,val){
        state.uploadInProgress = val;
    },
    setUploadPercentage(state,val){
        state.uploadPercentage = val + '%';
    },
    setInstructorOverride(state,val){
        debugger;
        state.instructorOverride = val;
    }
}

function getWeekNumber(d) {
    // Copy date so don't modify original
    d = new Date(Date.UTC(d.getFullYear(), d.getMonth(), d.getDate()));
    // Set to nearest Thursday: current date + 4 - current day number
    // Make Sunday's day number 7
    d.setUTCDate(d.getUTCDate() + 4 - (d.getUTCDay()||7));
    // Get first day of year
    var yearStart = new Date(Date.UTC(d.getUTCFullYear(),0,1));
    // Calculate full weeks to nearest Thursday
    var weekNo = Math.ceil(( ( (d - yearStart) / 86400000) + 1)/7);
    // Return array of year and week number
    return [d.getUTCFullYear(), weekNo];
}

export default {
    namespaced: true,
    state,
    getters,
    actions,
    mutations
  }

  function uuidv4() {
    return ([1e7]+-1e3+-4e3+-8e3+-1e11).replace(/[018]/g, c =>
        (c ^ crypto.getRandomValues(new Uint8Array(1))[0] & 15 >> c / 4).toString(16)
    ) 
  }