<template>
    <div class="instructor">
        <br />
        <br />
        <div class="row">
            <div class="col">
                <p>Instructor</p>
            </div>
        </div>
        <div class="row">
            <div class="col">
                <div v-for="(student,key) in myStudents" :key="key" class="card">
                    <div class="card-header" @click="toggleBody(key,$event)">
                        {{student.name}}
                    </div>
                    <div hidden="true" class="card-body">
                        <div v-for="(sheet,i) in sheets" :key="i">
                            <a v-on:click="goToSheet(i,key,$event)" href="">{{sheet.entryName + ' ' + formatWeek(sheet.week)}}</a>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <br />
        <br />
        <br />
    </div>
</template>

<script>

import { mapState } from 'vuex';

export default {
  name: 'Instructor',
  props: {
  },
  computed: mapState({
    uid: state => state.user.user.uid,
    myStudents: state => state.instructor.myStudents,
    sheets: state => state.instructor.sheets
  }),
  beforeMount(){
  },
  created: function(){
  },
  data: function(){
    return {
        sheetRetrieval: {}
    }
  },
  methods: {
    getSheets(studentId){
        this.$store.dispatch('instructor/getSheets',{uid:this.uid,studentId:studentId});
    },
    formatWeek(week){
        if(week){
        let weekParts = week.split('-W');
        let startDate = this.getDateOfWeek(weekParts[1],weekParts[0]),
            startDateString = (startDate.getMonth()+1) + '/' + startDate.getDate() + '/' + startDate.getFullYear();
        startDate.setDate(startDate.getDate()+7);
        let endDateString = (startDate.getMonth()+1) + '/' + startDate.getDate() + '/' + startDate.getFullYear();
        return startDateString + ' - ' + endDateString;
        }
    },
    getDateOfWeek(w, y) {
        var d = (1 + (w - 1) * 7); // 1st of January + 7 days for each week
        return new Date(y, 0, d);
    },
    toggleBody(key,ev){
        if(!this.sheetRetrieval[key]){
            this.sheetRetrieval[key] = true;
            this.getSheets(key);
        }
        ev.target.nextElementSibling.hidden = !ev.target.nextElementSibling.hidden;
    },
    goToSheet(sheetRef,key,ev){
        if(ev){
            ev.preventDefault();
        }
        this.$store.commit('sheet/setActiveSheetId',sheetRef);
        this.$store.commit('sheet/setActiveSheet',this.sheets[sheetRef]);
        this.$store.commit('container/setInstructor', false);
        this.$store.commit('container/setSheet', true);
        this.$store.commit('sheet/setInstructorOverride', key);
      }
  }
}

</script>

<style scoped>
    .card-header {
        cursor:pointer;
    }
</style>