const state = {
    instructors: {},
    myStudents: {},
    isInstructor: false,
    sheets: {}
}

import * as firebase from 'firebase'

var database;

  // getters
const getters = {}

// actions
const actions = {
    getInstructors({ commit }) {
      if(!database){ //initialize this once on login, call rootstate to access one database perhaps?
        database = firebase.database();
      }
      database.ref('instructors').on("value", snapshot =>{

        let instructors = snapshot.val();

        commit('setInstructors', instructors);
        
        }, err =>{
            console.log('read failed');
        });
    },
    checkIfInstructor({ commit, rootState }, uid) {
        if(!database){
            database = firebase.database();
        }    
        //rootState.user.user.uid
        database.ref('instructors/'+uid).on("value", snapshot =>{
    
            if(snapshot.exists()){
                commit('isInstructor', true);
            }
            else {
                commit('isInstructor', false);
            }
            
            }, err =>{
                console.log('read failed');
        });
    },
    getStudents({ commit }, uid) {
        if(!database){ //initialize this once on login, call rootstate to access one database perhaps?
            database = firebase.database();
        }
        if(this.myStudents && Object.keys(this.myStudents).length){
            return false;
        }
        database.ref('studentsByInstructor/'+uid).on("value", snapshot =>{

        let students = snapshot.val();

        commit('setStudents', students);
        
        }, err =>{
            console.log('read failed');
        });    
    },
    getSheets({ commit }, info) {
        if(!database){ //initialize this once on login, call rootstate to access one database perhaps?
            database = firebase.database();
        }
        database.ref('users/'+info.studentId+'/sheets').on("value", snapshot =>{
            let sheets = snapshot.val();
            commit('setSheets', sheets);
        }, err =>{
            console.log('read failed');
        }); 
    }
}

// mutations
const mutations = {
    addInstructor (state,instructor) {
      state.instructors.push(instructor);
    },
    setInstructors(state,instructors){
        state.instructors = instructors;
    },
    setStudents(state,students){
        state.myStudents = students;
    },
    isInstructor(state,val){
        state.isInstructor = val;
    },
    setSheets(state,val){
        state.sheets = val;
    }
}

export default {
    namespaced: true,
    state,
    getters,
    actions,
    mutations
  }