<script>
import { mapState } from 'vuex';

import AddVideo from './../icons/AddVideo.vue';
import Modal from '../steps/Modal.vue';


export default {
  name: 'Sheet',
  props: {
  },
  computed: mapState({
    sheet: state => state.sheet.activeSheet,
    sheetId: state => state.sheet.activeSheetId,
    userId: state => state.user.user.uid,
    userPin: state => state.user.user.pin,
    userPinVerified: state => state.user.user.pinVerified,
    instructor: state => state.user.user.instructor,
    instructorOverride: state => state.sheet.instructorOverride,
    uploadInProgress: state => state.sheet.uploadInProgress,
    uploadPercentage: state => state.sheet.uploadPercentage,
    weekString(){
        if(this.sheet.week){
            let weekParts = this.sheet.week.split('-W');
            let startDate = this.getDateOfWeek(weekParts[1],weekParts[0]),
                startDateString = (startDate.getMonth()+1) + '/' + startDate.getDate() + '/' + startDate.getFullYear();
            startDate.setDate(startDate.getDate()+7);
            let endDateString = (startDate.getMonth()+1) + '/' + startDate.getDate() + '/' + startDate.getFullYear();
            return startDateString + ' - ' + endDateString;
        }
        
        return '';
    }
  }),
  components: {
      AddVideo,
      Modal
  },
  beforeMount(){
      this.$store.dispatch('user/getPin',this.userId);
      console.log(this.userPin);
  },
  created: function(){
  },
  data: function(){
    return {
        week:'',
        strategyToggle:{},
        pieceToggle:{},
        temp:{
            show:false
        },
        uploadRef:{},
        showModal:false
    }
  },
  methods: {
    checkPin(){
        let pin = event.target.parentElement.parentElement.getElementsByClassName('my-modal-body')[0].getElementsByTagName('input')[0].value;
        if(pin === this.userPin){
            this.$store.dispatch('user/updatePinVerified');
        }
        else{
            alert('incorrect pin entered');
        }
        //check pin
        this.showModal = false;
    },
    updatePieceNotes(index){
        this.$store.dispatch('sheet/updatePieceNotes',{
            uid:this.userId,
            sheetId:this.sheetId,
            pieceIndex:index,
            notes:event.target.value
        });
    },
    test(){
        event.preventDefault();
        this.temp.show = !this.temp.show;
    },
    getDateOfWeek(w, y) {
        var d = (1 + (w - 1) * 7); // 1st of January + 7 days for each week
        return new Date(y, 0, d);
    },
    shareChange(el){
        this.$store.dispatch('sheet/updateShare',{uid:this.userId,sheetId:this.sheetId,shared:el.target.checked});
    },
    weekChange(el){
        this.$store.dispatch('sheet/updateWeek',{uid:this.userId,sheetId:this.sheetId,week:el.target.value});
    },
    handleStrategyToggle(ref){
        this.strategyToggle[ref] = !this.strategyToggle[ref];
        this.strategyToggle = {... this.strategyToggle};
    },
    handlePieceToggle(ref){
        this.pieceToggle[ref] = !this.pieceToggle[ref];
        this.pieceToggle = {... this.pieceToggle};
    },
    formatDescription(description){
        description = description.replace(/\n/g,'<br />');
        return description;
    },
    updateTime(sheetIndex,measureIndex,strategyIndex,ind){
            //let sheetCopy = { ...this.sheet };
            //move this logic to mutation state?
            this.$store.dispatch('sheet/updateMeasurePracticeTime',
            {
                uid:this.userId,
                measureIndex:measureIndex,
                strategyIndex:strategyIndex,
                ind:ind,
                sheetId:this.sheetId,
                sheet:this.sheet[sheetIndex],
                sheetRef:sheetIndex,
                val:event.target.value
            });
    },
    addVideo(ev){
        this.$store.dispatch('sheet/uploadVid',{
            vid:ev.target.files[0],
            userId:this.userId,
            uploadInfo:this.uploadRef,
            sheetId:this.sheetId
        });
    },
    setUploadInfo(type,index,measureIndex,stratIndex){
        console.log('type',type);
        console.log('index',index);
        this.uploadRef = {
            type:type,
            index:index,
            measureIndex:measureIndex,
            stratIndex:stratIndex
        };
    }
  }
}

</script>

