const state = {
    user: {
        phoneNumber:null,
        providerData:null,
        name:null,
        email:null,
        photoUrl:null,
        uid:null,
        instrument:null,
        ageRange:null,
        skillLevel:null,
        entries:{},
        customPieces:[],
        customStrategies:[]
    },
    updatedSuccess:false
}

import * as firebase from 'firebase'

var database;

  // getters
const getters = {}

// actions
const actions = {
    removeEntrySheet({ commit }, info){
        if(!database){
            database = firebase.database();
        }
        database.ref('users/' + info.userUID + '/sheets/' + info.id).remove();
    },
    getPin({commit}, uid){
        if(!database){
            database = firebase.database()
        }
        database.ref('pins/'+uid+'/pin').on('value', snapshot =>{
            let pin = snapshot.val();
            commit('updatePin',pin);
        }, err =>{
            console.log(err);
        });
    },
    getUserInfo({ commit }, userInfo) {
      if(!database){
        database = firebase.database()
      }
      database.ref('users/' + userInfo.uid).on("value", snapshot =>{
        let userInf = snapshot.val();
        let userRef = { ...userInfo };
        userRef.instrument = userInf.instrument || '';
        userRef.ageRange = userInf.ageRange || '';
        userRef.skillLevel = userInf.skillLevel || ''; 
        userRef.instructor = userInf.instructor || '';
        userRef.entries = userInf.entries;

        userRef.subscription = userInf.subscription;
        userRef.subscriptionActive = userInf.subscriptionActive;
        userRef.subscriptionCancelled = userInf.subscriptionCancelled;

        userRef.pin = '';
        userRef.pinVerified = false;

        checkInitiateVideoObjects(userInf.sheets);

        userRef.sheets = userInf.sheets;

        if(!userInf.customPieces){
            userInf.customPieces = {};
        }
        if(!userInf.customStrategies){
            userInf.customStrategies = {};
        }
        if(!userRef.entries){
            userRef.entries = {};
        }
        
        userRef.customPieces = Object.keys(userInf.customPieces).reduce((cPieces,key)=>{
            cPieces.push({
                id:userInf.customPieces[key].id,
                name:userInf.customPieces[key].name,
                author:userInf.customPieces[key].author || "",
                link:"",
                measures:[],
                notes:"",
                source:userInf.customPieces[key].source || "User Entry",
                strategies:[],
                tempo:"",
                custom:true,
                master:userInf.customPieces[key].master || false
            });
            return cPieces;
        },[]);
        userRef.customStrategies = Object.keys(userInf.customStrategies).map(key=>{
            userInf.customStrategies[key].custom = true;
            return userInf.customStrategies[key];
        });

        Object.keys(userRef.entries).map(key=>{
            Object.keys(userRef.entries[key]).map(key2=>{
                if(key2 !== 'entryName'){
                    if(!userRef.entries[key][key2].measures && !userRef.entries[key][key2].strategies){
                        userRef.entries[key][key2].measures = [];
                        userRef.entries[key][key2].strategies = [];
                    }
                }
            });
        });

/*         userRef.entries.map(entry=>{
            if(!entry.measures && !entry.strategies){
                entry.strategies = [];
            }
        }); */

        commit('setUser', userRef);
        
        }, err =>{
            console.log('read failed');
        });
    },
    updatePinVerified({ commit }){
        commit('setPinVerified',true);
    },
    setPin({ commit }, info){
        if(!database){
            database = firebase.database();
        }
        return database.ref('pins/' + info.user.uid).update({
            pin:info.pin,
            existingPin:info.existingPin
        });
    },
    setUserProfile({ commit }, user) {
        if(!database){
            database = firebase.database();
        }
        if(state.user.instructor !== user.instructor){
            if(state.user.instructor){
                database.ref('studentsByInstructor/' + state.user.instructor + '/' + user.uid).remove();
            }
            if(user.instructor){
                database.ref('studentsByInstructor/' + user.instructor + '/' + user.uid).set({
                    name: user.name,
                    visibleSheets: "ALL"
                });
            }
        }


        database.ref('users/' + user.uid).update({
            instrument: user.instrument,
            ageRange: user.ageRange,
            skillLevel : user.skillLevel,
            instructor:user.instructor
        }).then(()=>{
            commit('setUser', user);
            commit('updateSuccess', true);
            setTimeout(()=>{
                commit('updateSuccess', false);
            },1000);
        });
    },
    setUserInfo({ commit }, user) {
        if(!database){
            database = firebase.database()
        }
        database.ref('users/' + user.uid).update({
            username: user.name,
            email: user.email,
            profile_picture : user.photoUrl
        });
    }
}

// mutations
const mutations = {
    setUser (state, userInfo) {
      state.user = userInfo;
    },
    updateSuccess (state, val) {
        state.updatedSuccess = val;
    },
    updatePin (state, pin){
        state.user.pin = pin;
    },
    setPinVerified (state, val){
        state.user.pinVerified = val;
    }
}

export default {
    namespaced: true,
    state,
    getters,
    actions,
    mutations
  }

function checkInitiateVideoObjects(sheets){
    Object.keys(sheets)
    .map(sheet => {
        Object.keys(sheets[sheet])
        .filter(sheetIndex => {
            return !isNaN(parseInt(sheetIndex))
        })
        .map(sheetIndex => {
            if(!sheets[sheet][sheetIndex].videos){
                sheets[sheet][sheetIndex].videos = {};    
            }
            if(sheets[sheet][sheetIndex].strategies){
                Object.keys(sheets[sheet][sheetIndex].strategies).map((strategy)=>{
                    if(!sheets[sheet][sheetIndex].strategies[strategy].videos){
                        sheets[sheet][sheetIndex].strategies[strategy].videos = {};    
                    }
                });                
            }
            if(sheets[sheet][sheetIndex].measures){
                Object.keys(sheets[sheet][sheetIndex].measures).map((measure)=>{
                    if(!sheets[sheet][sheetIndex].measures[measure].videos){
                        sheets[sheet][sheetIndex].measures[measure].videos = {};    
                    }
                    if(sheets[sheet][sheetIndex].measures[measure].strategies){
                        Object.keys(sheets[sheet][sheetIndex].measures[measure].strategies).map((strategy)=>{
                            if(!sheets[sheet][sheetIndex].measures[measure].strategies[strategy].videos){
                                sheets[sheet][sheetIndex].measures[measure].strategies[strategy].videos = {};
                            }
                        });
                    }
                });
            }
        });
    });
}