<template>
    <div class="profile">
        <br />
        <br />
        <div class="row">
            <div class="col-md-8 offset-md-2 col-sm-7 offset-sm-3 form-box">
                <p>Profile</p>
            </div>
            <div class="col-md-8 offset-md-2 col-sm-7 offset-sm-3">
                <div class="card">
                    <div class="card-header">
                        <p v-if="user.name">{{user.name}}</p>
                    </div>
                    <div class="card-body">
                        <button v-if="user.subscriptionActive && !user.subscriptionCancelled" v-on:click="openCancelModal()" class="float-right btn btn-sm btn-success btn-subscribe">Cancel Subscription</button>
                        <button v-if="!user.subscriptionActive && !isInstructor" v-on:click="openPaymentModal()" class="float-right btn btn-sm btn-success btn-subscribe">Subscribe</button>
                        <button v-if="!isInstructor" v-on:click="openPinModal()" class="float-right btn btn-sm btn-info">Set Protection Pin</button>
                        <div v-if="user.subscriptionCancelled" class="expiration">
                            <br />
                            <span>Subscription set to expire</span>
                            <br />
                        </div>
                        <br />
                        <form>
                            <div class="form-group">
                                <label for="formInstrument">Instrument</label>
                                <input :value="user.instrument" @input="instrument = $event.target.value" type="text" class="form-control" id="formInstrument" placeholder="Specify Instrument">
                            </div>
                            <div class="form-group">
                                <label for="formAgeRange">Age Range</label>
                                <select :value="user.ageRange" @input="ageRange = $event.target.value" class="form-control" id="formAgeRange">
                                    <option value="">Select Age Range</option>
                                    <option value="U8">Under 8</option>
                                    <option value="8to11">8-11</option>
                                    <option value="12to17">12-17</option>
                                    <option value="18to24">18-24</option>
                                    <option value="25to37">25-37</option>
                                    <option value="38+">38+</option>
                                </select>
                            </div>
                            <div class="form-group">
                                <label for="formSkillLevel">Skill Level</label>
                                <select :value="user.skillLevel" @input="skillLevel = $event.target.value" class="form-control" id="formSkillLevel">
                                    <option value="">Select Skill Level</option>
                                    <option>Beginner</option>
                                    <option>Competent</option>
                                    <option>Proficient</option>
                                    <option>Expert</option>
                                </select>
                            </div>
                            <div v-if="!isInstructor" class="form-group">
                                <label for="formInstructor">Instructor</label>
                                <select :value="user.instructor" @input="instructor = $event.target.value" class="form-control" id="formInstructor">
                                    <option value="">Select Instructor</option>
                                    <option :selected="i === user.instructor" v-for="(instructor,i) in instructors" :key="i" :value="i">{{instructor.name}}</option>
                                </select>
                            </div>
                            <transition name="fade">
                                <div v-show="updatedSuccess" class="alert alert-success" role="alert">
                                    Update Successful
                                </div>
                            </transition>
                            <button v-on:click="update()" class="btn btn-primary">Update</button>
                            <br />
                            <br />
                        </form>
                    </div>
                </div>
            </div>
        </div>
        <br />
        <br />
        <UpdateSetPinModal @close="closeModal()" v-if="updatePinModalIsVisible">
            <h3 slot="header">{{user.pin ? 'Update Pin' : 'Set Pin'}}</h3>
            <div slot="body">
                <input v-if="user.pin" maxlength="5" v-model="userExistingPin" type="text" placeholder="Existing Pin" />
                <input v-model="userNewPin" maxlength="5" type="text" placeholder="New Pin" />
            </div>
        </UpdateSetPinModal>
        <Modal @close="closePayModal()" v-if="payModalVisible" btnText="Close" wide="true">
                <h3 slot="header">Subscribe</h3>
                <div class="card" slot="body">
                    <div class="card-header">
                        Payment Details
                    </div>
                    <div v-if="loading">
                        <br />
                        <img class="loading" src="./../../assets/loading.gif"> 
                    </div>
                    <div v-if="!loading">
                        <br />
                        <p>Monthly Subscription $12.99<br/> Unlimited sheets and video uploads plus access to exclusive content!</p>
                        <div class="card-body">
                            <card class='stripe-card'
                            :class='{ complete }'
                            stripe='pk_test_OVtqZNDqKtdgnVHmYWv5BtEP'
                            :options='stripeOptions'
                            @change='complete = $event.complete'
                            />
                            <br />
                            <button class='pay-with-stripe btn btn-success' @click='pay' :disabled='!complete'>Process Subscription</button>
                        </div>
                    </div>
                </div>
        </Modal>
        <Modal class="cancel-modal" @close="closeCancelModal()" v-if="cancelModalVisible" btnText="Close" wide="true">
            <h3 slot="header">Cancel Subscription</h3>
            <div class="card" slot="body">
                <br />
                <div v-if="loading">
                    <img class="loading" src="./../../assets/loading.gif"> 
                </div>
                <div v-if="!loading">
                    <button class='btn btn-success' @click='cancelSubscription' :disabled='!user.subscriptionActive'>Cancel Subscription</button>
                    <br />
                </div>
            </div>
        </Modal>
    </div>
</template>

<script>

import { Card, createToken } from 'vue-stripe-elements'

import { mapState, mapActions } from 'vuex';

import UpdateSetPinModal from  './UpdateSetPinModal.vue';
import Modal from '../steps/Modal.vue';
import * as firebase from 'firebase';


export default {
  name: 'Profile',
  components: {
    UpdateSetPinModal,
    Modal,
    Card
  },
  props: {
  },
  beforeMount(){
      this.instructor = this.user.instructor;
  },
  created: function(){
  },
  data: function(){
    return {
        loading:false,
        payModalVisible:false,
        cancelModalVisible:false,
        instrument:'',
        ageRange:'',
        skillLevel:'',
        instructor:'',
        updatePinModalIsVisible:false,
        userNewPin:'',
        userExistingPin:'',
        complete: false,
        stripeOptions: {
        // see https://stripe.com/docs/stripe.js#element-options for details
        }
        //updatedSuccess:false
    }
  },
  mounted:function(){
    this.$store.dispatch('instructor/getInstructors');
    this.$store.dispatch('user/getPin',this.user.uid);
  },
  computed: mapState({
    user: state => state.user.user,
    updatedSuccess: state => state.user.updatedSuccess,
    instructors: state => state.instructor.instructors,
    isInstructor: state => state.instructor.isInstructor
  }),
  methods: {
      cancelSubscription(){
        this.loading = true;
        const cancelSubscription = 
        firebase.functions()
        .httpsCallable('cancelSubscription');
        cancelSubscription(this.user.subscription).then(response=>{
            console.log('response: ',response);
            alert('Your subscription will not be renewed, but will remain active until your subscription expires.');
            this.closeCancelModal();
            this.loading = false;
        });

      },
      openCancelModal(){
          this.cancelModalVisible = true;
      },
      closeCancelModal(){
          this.cancelModalVisible = false;
      },
      pay () {
      // createToken returns a Promise which resolves in a result object with
      // either a token or an error key.
      // See https://stripe.com/docs/api#tokens for the token object.
      // See https://stripe.com/docs/api#errors for the error object.
      // More general https://stripe.com/docs/stripe.js#stripe-create-token.
        createToken().then(data => {
            this.loading = true;
            //this.$store.dispatch('');
            console.log(data.token);
            const processSubscription = 
            firebase.functions()
            .httpsCallable('processSubscription');
            processSubscription({ token:data.token }).then(response=>{
                console.log('response: ',response);
                alert('You have successfully subscribed!');
                this.closePayModal();
                this.loading = false;
            });
        });
      },
      update() {
        //
        event.preventDefault();
        let usr = { ...this.user };
        //let usr = {uid:this.user.uid};
        usr.instrument = this.instrument.trim() ? this.instrument : usr.instrument;
        usr.ageRange = this.ageRange.trim() ? this.ageRange : usr.ageRange;
        usr.skillLevel = this.skillLevel.trim() ? this.skillLevel : usr.skillLevel;
        usr.instructor = this.instructor;

        this.$store.dispatch('user/setUserProfile',usr);

      },
      openPinModal(){
          this.updatePinModalIsVisible = true;
      },
      openPaymentModal(){
          this.payModalVisible = true;
      },
      closePayModal(){
          this.payModalVisible = false;
      },
      closeModal(){
        if(!this.userNewPin && !this.userExistingPin){
            this.userNewPin = '';
            this.userExistingPin = '';
            this.updatePinModalIsVisible = false;
        }
        else if(this.userNewPin){
            if(isNaN(parseInt(this.userNewPin))){   
                alert('pin must be a number');
                return false;
            }
            this.updatePinModalIsVisible = false;
            this.$store.dispatch('user/setPin',{user:this.user,pin:this.userNewPin,existingPin:this.userExistingPin})
            .then(()=>{
                this.userNewPin = '';
                this.userExistingPin = '';
                //update usr pin
            },err=>{
                this.updatePinModalIsVisible = true;
                alert(err);
            });
        }
      }
  }
}

</script>

<style scoped>
    .card-body button {
        margin-top:-10px;
    }
    .btn-subscribe {
        margin-left:5px;
    }
    .stripe-card {
    width: 300px;
    margin:0 auto;
    }
    .stripe-card.complete {
    border-color: green;
    }
    .loading {
        width:120px;
        margin:0 auto;
    }
    .cancel-modal .card {
        padding:20px!important;
    }
    .expiration span {
        color:red;
        font-size:16px;
    }
</style>